@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;500;900&display=swap);
:root {
  --font-family: "Manrope", sans-serif;

  --gradient-text: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #ae67fa -13.86%, #f49867 99.55%);

  --color-bg: #040c18;
  --color-footer: #031b34;
  --color-blog: #042c54;
  --color-text: #81afdd;
  --color-subtext: #ff8a71;
}

* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
  scroll-behavior: smooth;
}

body {
  background: var(--color-bg);
}

html,
body {
  width: auto !important;
  overflow-x: hidden !important;
}

a {
  color: unset;
  text-decoration: none;
}
.gradient__bg {
  /* ff 3.6+ */

  /* safari 5.1+,chrome 10+ */

  /* opera 11.10+ */

  /* ie 10+ */

  /* global 92%+ browsers support */
  background: radial-gradient(
    circle at 3% 25%,
    rgba(6, 75, 149, 1) 0%,
    rgba(4, 12, 24, 1) 100%
  );
}
.gradient__text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.scale-up-center {
  animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

@keyframes scale-up-center {
  0% {
    transform: scale(0.5);
  }
  100% {
    transform: scale(1);
  }
}


.ucsp__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem 2rem;
}
.ucsp__navbar-links {
  flex: 0.8 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.ucsp__navbar-links_logo {
  margin-right: 2rem;
  margin-left: 2rem;
}
.ucsp__navbar-links_logo img {
  object-fit: contain;
  height: 80px;
}

.ucsp__navbar-links_container {
  display: flex;
  margin-left: 3rem;
}
.ucsp__navbar-links_container a:hover {
  cursor: pointer;
  /* font-size: 22px; */
  border: 1px solid rgb(8, 31, 238);
  border-radius: 5px;
  background: rgb(155, 230, 140);
  color: blue;
  padding: 5px 12px;
}
.ucsp__navbar-sign {
  flex: 0.2 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.ucsp__navbar-links_container p,
.ucsp__navbar-sign p,
.gpt__navbar-menu_container p {
  color: #fff;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;
  margin: 0 1rem;
  cursor: pointer;
}
.ucsp__navbar-sign button,
.ucsp__navbar-menu_container-links button {
  padding: 0.5rem 1rem;
  color: #fff;
  background: #ff4820;
  font-family: var(--font-family);

  border-radius: 5px;
  font-weight: 500;
  border: none;
  font-size: 18px;
  line-height: 25px;
  outline: none;
  cursor: pointer;
}
.ucsp__navbar-menu {
  margin-left: 1rem;
  display: none;
  position: relative;
}

.ucsp__navbar-menu svg {
  cursor: pointer;
}

.ucsp__navbar-menu_container-links {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  text-align: end;
  background: var(--color-footer);
  padding: 2rem;
  position: absolute;
  top: 40px;
  right: 0px;
  /* z-index: 50px; */
  color: #fff;
  margin-top: 1rem;
  min-width: 210px;
  border-radius: 5px;
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.ucsp__navbar-menu_container-links p {
  margin: 1rem 0;
  font-size: 21px;
}
.ucsp_navbar-menu_container-links-sign {
  display: none;
}

@media screen and (max-width: 1050px) {
  .ucsp__navbar-links_container {
    display: none;
    transition: ease-in-out 4sec;
  }
  .ucsp__navbar-menu {
    display: flex;
  }
  .ucsp__navbar-sign {
    flex: 0.6 1;
    margin-right: 2rem;
  }
}
@media screen and (max-width: 700px) {
  .ucsp__navbar {
    padding: 2rem 4rem;
  }
  .ucsp__navbar-sign {
    margin-right: 1rem;
  }
}
@media screen and (max-width: 550px) {
  .ucsp__navbar {
    padding: 2rem;
  }
  .ucsp__navbar-sign {
    display: none;
  }
  .ucsp__navbar-menu_container {
    top: 20px;
  }
  .ucsp__navbar-menu_container-links-sign {
    display: block;
  }
}

