.heading {
  /* display:none; */
  margin-top: 5px;
  font-size: 2.8rem;
  font-family: sans-serif;
  font-weight: bolder;
  width: 18rem;
  margin: auto;
  /* padding: .5rem; */
  border-bottom: 1px solid rgb(240, 77, 27);
  color: #fff;
  text-align: center;
}
.heading:hover {
  animation: tracking-in-contract 1.5s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}
@keyframes tracking-in-contract {
  0% {
    letter-spacing: 1em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    letter-spacing: normal;
    opacity: 1;
  }
}
.card-container {
  position: relative;
  width: 100%;

  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  padding: 20px;
  margin-top: 10px;
}

.newevent {
  max-width: 450px;
  margin: auto;
  position: relative;
  height: 350px;

  margin: 30px auto;
  padding: 20px 15px;
  position: relative;
  z-index: -1;
}
.jello-vertical {
  animation: jello-vertical 0.9s 1s both;
}
@keyframes jello-vertical {
  0% {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(0.75, 1.25, 1);
  }
  40% {
    transform: scale3d(1.25, 0.75, 1);
  }
  50% {
    transform: scale3d(0.85, 1.15, 1);
  }
  65% {
    transform: scale3d(1.05, 0.95, 1);
  }
  75% {
    transform: scale3d(0.95, 1.05, 1);
  }
  100% {
    transform: scale3d(1, 1, 1);
  }
}
.newevent img {
  height: 300px;
  max-width: 100%;
  border-radius: 10px;
  border: 2px solid white;
  box-shadow: 0 5px 20px rgba(248, 246, 246, 0.5);
}

@media screen and (max-width: 480px) {
  .newevent img {
    height: 250px;
    width: 100%;
  }
}

