  z-index: 1;
}

.about_cards_container .card .image img {
  max-width: 100%;
  border-radius: 15px;
}

.about_cards_container .card .content {
  position: relative;
  top: -165px;
  padding: 0px 15px;
  color: #111;
  text-align: center;

  visibility: hidden;
  opacity: 0;
  transition: 0.3s ease-in-out;
