.nav{
  justify-content: center;
  align-items: center;
  text-align: center;
}

.nav_ul {
  list-style-type: none;
  align-items: center;
  text-align: center;
  justify-content: center;
  display: flex;
  /* margin: 0;
    padding: 0; */
}

.nav_ul_li {
  --c: #ff8a71;
  color: var(--c);
  font-size: 16px;
  border: 0.1em solid var(--c);
  border-radius: 0.5em;
  width: 12em;
  height: 3em;
  text-transform: uppercase;
  /* font-weight: bold; */
  font-family: sans-serif;
  letter-spacing: 0.1em;
  text-align: center;
  line-height: 3em;
  position: relative;
  overflow: hidden;
  z-index: 1;
  transition: 0.5s;
  margin: 1em;
}

.nav_ul_li_span {
  position: absolute;
  /* width: 25%; */
  height: 100%;
  background-color: var(--c);
  transform: translateX(0%);
  border-radius: 0%;
  left: calc((var(--n) - 1) * 25%);
  transition: 0.5s;
  transition-delay: calc((var(--n) - 1) * 0.1s);
  z-index: -1;
}

.nav_ul_li:hover {
  color: black;
}

.nav_ul_li:hover span {
  transform: translateX(0) scale(10);
  width: 25%;
}

.nav_ul_li span:nth-child(1) {
  --n: 1;
}

